export default {
  props: {
    switches: {
      type: Array,
      default () {
        return [{
          name: '项目列表'
        }, {
          name: 'EPS'
        }]
      }
    },
    currentIndex: {
      type: Number,
      default: 0
    },
    lineHeight: {
      type: Number,
      default: 1
    },
    switcheWidth: {
      type: String,
      default: '100%'
    }
  },
  methods: {
    switchItem (index) {
      this.$emit('switch', index)
    }
  }
}



